* {
  font-family: "Tenor Sans", sans-serif;
}

.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}

.active {
  text-decoration: line-through;
  opacity: 0.4;
  color: red;
}

.form_body {
   
  /* border: 1px solid black; */
  width: 360px;
  border-radius: 75px 10px 10px 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  background: #f1f3a2;
}
form {
  /* border: 1px solid black; */
  text-align: center;
  width: 330px;
  height: 115px;

}

.task {
  border-radius: 3px;
  width: 350px;
  display: flex;
  margin-top: 5px;
  justify-content: center;
  align-items: center;
  background-color: rgba(241, 240, 244, 0.674);
}

.btn {
  /* margin-left: auto; */
  margin: 2px;
  padding: 2px;
  cursor: pointer;
  /* font-size: 20px; */
  width: 25px;
  height: 20px;
  color: red;
  background-color: rgba(221, 214, 214, 0.536);
  border-radius: 3px;
}

/* span {
  margin-left: 5px;
} */

.static {
  display: flex;
  justify-content: space-evenly;
}

.content {
  display: flex;
  align-items: center;
  width: 320px;
  }
  

.pen {
  color: green;
  margin-right: 5px;
  /* margin-top: 2px; */
  cursor: pointer;
  background-color: rgba(221, 214, 214, 0.536);
  border-radius: 3px;
  /* margin: 2px; */
  /* font-size: 20px; */
  width: 25px;
  height: 20px;
  padding: 2px;
}

.inp {
  width: 242px;
  height: 20px;
  outline: none;
  border-radius: 6px;
  padding: 4px;
  border-color: rgb(214, 204, 204);
}

.btn_addtodo {
  border: none;
  background-color: green;
  color: white;
  border-radius: 6px;
  padding: 8px;
}

.btn_save {
  background-color: green;
  color: white;
  border: none;
  height: 20px;
  border-radius: 5px;
  cursor: pointer;
}

.inp_edit {
  outline: none;
}

.span_text:hover {
  background-color: yellow;
}

.date {
  font-size: 10px;
  opacity: 0.6;
}

p {
  padding: 0;
  margin: 0;
}

.update {
  width: 200px;
  height: 50px;
  background-color: #ffc107;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  margin-left: 10px;
  margin-top: 10px;
}

.footer {
  width: 352px;
  display: flex;
  
}

.content_body {
max-height: 230px;
overflow: auto;
}

.inp_index {
  display: flex;
  width: 50px;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
}

